<template>
	<gracePage :customHeader="false" :isLoading="pageLoading">
		<view slot="gBody" class="grace-body">

				<view class="grace-margin-top">当前登录用户：{{ userInfo.UserName }}</view>

				<view>当前项目：<text>{{ currentProjectInfo.Name }}</text></view>
				<!-- <view class="ucenter-line"></view> -->

				<view class="grace-card-view">
					<view class="body">
						<view v-if="currentProjectInfo.OrderStatusCode == 100" class="img">
							<image src="../../static/icon/icon-wms-done.png" />
							<view>已结束</view>
						</view>
						<view v-else-if="currentProjectInfo.OrderStatusCode == 20" class="img">
							<image src="../../static/icon/icon-wms-inprocess.png" />
							<view>进行中</view>
						</view>
						<view v-else class="img">
							<image src="../../static/icon/icon-wms-todo.png" />
							<view>未开始</view>
						</view>

						<view class="desc">
							<view class="grace-text">盘点类型：{{ currentProjectInfo.OrderTypeName }}</view>
							<view class="grace-text">开始时间：{{ currentProjectInfo.PlannedStartTime }}</view>
							<view class="grace-text">结束时间：{{ currentProjectInfo.PlannedEndTime }}</view>
						</view>
					</view>
				</view>
				
				
				<view>我的进度：</view>
				<view class="grace-box-banner">
					<view class="garce-items">
						<view class="line1">{{ myFirstCounted + mySecondCounted + myThirdCounted }}</view>
						<view class="line2">已完成</view>
					</view>
					<view class="garce-items">
						<view class="line1">{{ myFirstTotal + mySecondTotal + myThirdTotal }}</view>
						<view class="line2">全部</view>
					</view>
				</view>
				
				<view class="ucenter-line"></view>
				
				<view>
					我的初盘进度：
					<text class="grace-green">{{ myFirstCounted }}</text>/{{ myFirstTotal }}
					<progress :percent="myFirstProgress" show-info />
				</view>
				<view v-if="currentProjectInfo.IsSecondEnabled == 1">
					<view>
						我的复盘进度：
						<text class="grace-green">{{ mySecondCounted }}</text>/{{ mySecondTotal }}
						<progress :percent="mySecondProgress" show-info />
					</view>
				</view>
				<view v-if="currentProjectInfo.IsThirdEnabled == 1">
					<view>
						我的三盘进度：
						<text class="grace-green">{{ myThirdCounted }}</text>/{{ myThirdTotal }}
						<progress :percent="myThirdProgress" show-info />
					</view>
				</view>

				<view class="ucenter-line"></view>
				<view class="grace-margin-top">当前盘点总进度：</view>
<!-- 				<view class="grace-flex-vcenter">
					<view style="width:150px; margin: auto;">
						<graceCirProgress :width="150" canvasId="graceCirProgress1" :value="projectProgress" :lineWidth="20" progressColor="#10AEFF" fontColor="#10AEFF">
							<canvas style="width:150px; height:250px;" canvas-id="graceCirProgress1"></canvas>
						</graceCirProgress>
					</view>
				</view> -->
				
				<view class="grace-box-banner">
					<view class="garce-items">
						<view class="line1">{{ firstCounted + secondCounted + thirdCounted }}</view>
						<view class="line2">已完成</view>
					</view>
					<view class="garce-items">
						<view class="line1">{{ firstTotal + secondTotal + thirdTotal }}</view>
						<view class="line2">全部</view>
					</view>
				</view>
				
				<view class="ucenter-line"></view>

				<view class="grace-margin-top">
					初盘总进度：
					<text class="grace-green">{{ firstCounted }}</text>/{{ firstTotal }}
					<progress :percent="firstProgress" show-info />
				</view>
				<view v-if="projectStatistic.IsSecondEnabled == 1" class="grace-margin-top">
					复盘总进度：
					<text class="grace-green">{{ secondCounted }}</text>/{{ secondTotal }}
					<progress :percent="secondProgress" show-info />
				</view>
				<view v-if="projectStatistic.IsThirdEnabled == 1" class="grace-margin-top">
					三盘总进度：
					<text class="grace-green">{{ thirdCounted }}</text>/{{ thirdTotal }}
					<progress :percent="thirdProgess" show-info />
				</view>

				<view class="ucenter-line"></view>


				<view class="grace-margin-top grace-margin-bottom"></view>

		</view>
	</gracePage>
</template>

<script>
import graceCirProgress from '../../graceUI/components/graceCirProgress.vue';
import graceFlex from '../../graceUI/components/graceFlex.vue';
// import uniRequest from '../../http/uniRequest.js';
export default {
	data() {
		return {
			userInfo: {},
			projectList: [{}],
			projectIndex: 0,
			//currentProjectInfo: { OrderTypeName: '盘点类型', PlannedStartTime: '开始时间', PlannedEndTime: '结束时间' },
			currentProjectInfo: {},
			projectStatistic: {},
			projectProgress: 0,
			firstTotal: 0,
			secondTotal: 0,
			thirdTotal: 0,
			firstCounted: 0,
			secondCounted: 0,
			thirdCounted: 0,
			firstProgress: 0,
			secondProgress: 0,
			thirdProgess: 0,
			
			myFirstTotal: 0,
			mySecondTotal: 0,
			myThirdTotal: 0,
			myFirstCounted: 0,
			mySecondCounted: 0,
			myThirdCounted: 0,
			myFirstProgress: 0,
			mySecondProgress: 0,
			myThirdProgress: 0,
			
			pageLoading: false
		};
	},
	components: {
		graceCirProgress,
		graceFlex
	},
	onLoad() {
		this.pageOnLoad();
	},
	onShow() {
		
	},
	onPullDownRefresh: function() {
		this.pageOnLoad();
		console.log('刷新了');
		uni.stopPullDownRefresh();
	},
	methods: {
		pageOnLoad() {
			var me = this;
			me.pageLoading = true;

			var userInfo = me.getCurrentUserInfo();
			if (userInfo != null) {
				me.userInfo = userInfo;
				var serverUrl = me.getServerUrl();
				//读取ProjectList
				var currentProjectInfo = me.getCurrentProjectInfo();
				if (currentProjectInfo != null) {
					me.currentProjectInfo = currentProjectInfo;
					
					//获取当前用户盘点数据 - 开始
					me.getLineStatistics(currentProjectInfo.Id, 1, userInfo.UserName);
					if (currentProjectInfo.IsSecondEnabled == 1) {
						me.getLineStatistics(currentProjectInfo.Id, 2, userInfo.UserName);
					}
					if (currentProjectInfo.IsThirdEnabled == 1) {
						me.getLineStatistics(currentProjectInfo.Id, 3, userInfo.UserName);
					}
					//获取当前用户盘点数据 - 结束
					
					//获取盘点单盘点数据 - 开始
					me.getOrderStatistics(currentProjectInfo.Id);
					//获取盘点单盘点数据 - 结束
					
				} else {
					//跳转到piConfig
					setTimeout(function() {
						uni.reLaunch({
							url: '../pi/piconfig'
						});
					}, 1500);
				}
			} else {
				me.userInfo = {};
				//未登录，返回登陆界面
				uni.reLaunch({
					url: '../user/login'
				});
			}
			
			//me.pageLoading = false;
		},
		
		getLineStatistics(projectId, piShift, userName) {
			let me = this;
			let serverUrl = me.getServerUrl();
			let userInfo = me.getCurrentUserInfo();
		
			uni.request({
				url: serverUrl + '/PhysicalInventoryOrder/GetLineStatistics/' + projectId + '?sequence=' + piShift + '&groupBy=OperatorUserName',
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					if (res.data.Status) {
						if (res.data.StatusCode == 'UserNotLogin') {
							uni.removeStorageSync('currentUserInfo');
							// me.userIsLogin = false;
							// uni.showToast({
							// 	title: '用户已异地登录',
							// 	duration: 2000,
							// 	icon: 'none'
							// });
							me.showErrorMessage = !me.showErrorMessage;
							me.errorMessage = "用户已异地登录";
							setTimeout(() => {
								uni.reLaunch({
									url: "../user/login"
								})
							}, 1500);
						} else {
							if (res.data.Data == null) {
								//无数据
							} else {
								//console.log(res.data.Data);
								for (var i = 0; i < res.data.Data.length; i++) {
									if (res.data.Data[i].GroupBy == userName) {
										if (res.data.Data[i].TotalLines > 0) {
											var result = Math.ceil((res.data.Data[i].CountedLines / res.data.Data[i].TotalLines) * 100);
											if (piShift == 1) {
												me.myFirstCounted = res.data.Data[i].CountedLines;
												me.myFirstTotal = res.data.Data[i].TotalLines;
												me.myFirstProgress = result;
											}
											if (piShift == 2) {
												me.mySecondCounted = res.data.Data[i].CountedLines;
												me.mySecondTotal = res.data.Data[i].TotalLines;
												me.mySecondProgress = result;
											}
											if (piShift == 3) {
												me.myThirdCounted = res.data.Data[i].CountedLines;
												me.myThirdTotal = res.data.Data[i].TotalLines;
												me.myThirdProgress = result;
											}
										}
									}
								}
							}
						}
					} else {
						//debugger;
						uni.showToast({
							title: res.data.StatusMessage,
							duration: 2000,
							icon: 'none'
						});
						//return;
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});
		},
		
		getOrderStatistics(projectId) {
			let me = this;
			let serverUrl = me.getServerUrl();
			let userInfo = me.getCurrentUserInfo();

			uni.request({
				url: serverUrl + '/PhysicalInventoryOrder/GetStatistics/' + projectId,
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					if (res.data.Status) {
						if (res.data.StatusCode == 'UserNotLogin') {
							uni.removeStorageSync('currentUserInfo');
							// me.userIsLogin = false;
							// uni.showToast({
							// 	title: '用户已异地登录',
							// 	duration: 2000,
							// 	icon: 'none'
							// });
							me.showErrorMessage = !me.showErrorMessage;
							me.errorMessage = "用户已异地登录";
							setTimeout(() => {
								uni.reLaunch({
									url: "../user/login"
								})
							}, 1500);
						} else {
							if (res.data.Data == null) {
								uni.showToast({
									title: '此盘点无统计数据',
									duration: 1500,
									icon: 'none'
								});
								me.projectStatistic = {};
							} else {
								me.projectStatistic = res.data.Data;
								if (me.projectStatistic.TotalLines > 0) {
									me.projectProgress = Math.ceil((me.projectStatistic.CountedLines / me.projectStatistic.TotalLines) * 100);
								} else {
									me.projectProgress = 0;
								}
								if (me.projectStatistic.FirstTotalLines > 0) {
									me.firstCounted = me.projectStatistic.FirstCountedLines;
									me.firstTotal = me.projectStatistic.FirstTotalLines;
									me.firstProgress = Math.ceil((me.projectStatistic.FirstCountedLines / me.projectStatistic.FirstTotalLines) * 100);
								} else {
									me.firstProgress = 0;
								}
								if (me.projectStatistic.IsSecondEnabled == 1) {
									if (me.projectStatistic.SecondTotalLines > 0) {
										me.secondCounted = me.projectStatistic.SecondCountedLines;
										me.secondTotal = me.projectStatistic.SecondTotalLines;
										me.secondProgress = Math.ceil((me.projectStatistic.SecondCountedLines / me.projectStatistic.SecondTotalLines) * 100);
									} else {
										me.secondProgress = 0;
									}
								}
								if (me.projectStatistic.IsThirdEnabled == 1) {
									if (me.projectStatistic.ThirdTotalLines > 0) {
										me.thirdCounted = me.projectStatistic.ThirdCountedLines;
										me.thirdTotal = me.projectStatistic.FirstTotalLines;
										me.thirdProgess = Math.ceil((me.projectStatistic.ThirdCountedLines / me.projectStatistic.ThirdTotalLines) * 100);
									} else {
										me.thirdProgess = 0;
									}
								}
								//console.log(me.projectStatistic);
							}
						}
					} else {
						//debugger;
						uni.showToast({
							title: res.data.StatusMessage,
							duration: 2000,
							icon: 'none'
						});
						//return;
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});
			
			me.pageLoading = false;
		},
		goToProgessReport() {
			uni.reLaunch({
				url: '../reports/progress'
			});
		}
	},
	onShareAppMessage(res) {
		return {
			title: '盘点App - 一款省时，省力，省钱的仓库盘点工具',
			path: '/pages/help/help'
		};
	}
};
</script>

<style>
.progress {
	margin: 30rpx 0;
}

.three {
	border-right: 2rpx solid #f5f6f8;
	border-bottom: 2rpx solid #f5f6f8;
}

.grace-title {
	padding: 10rpx 0;
	line-height: 2em;
	display: flex;
	flex-wrap: nowrap;
	font-size: 28rpx;
	justify-content: space-between;
	font-weight: 500;
	align-items: center;
}

/* #ifdef H5 */
.grace-title {
	font-weight: 700;
}

/* #endif */
.grace-title > .title {
	width: 200rpx;
	flex: auto;
}

.grace-title > .icon {
	width: 50rpx;
	font-size: 50rpx;
}

.current-project {
	margin-left: 20upx;
	width: 170upx;
}

.three > .items {
	width: 33.333333%;
	border-left: 2rpx solid #f5f6f8;
	border-top: 2rpx solid #f5f6f8;
	color: #a5a7b2;
}

.contact {
	width: 100%;
	height: 100rpx;
	line-height: 100rpx;
	text-align: center;
	margin-top: 30rpx;
}

.demoForIcon {
	border-right: 2rpx solid #f5f6f8;
	border-bottom: 2rpx solid #f5f6f8;
}

.demoForIcon > .items {
	border-left: 2rpx solid #f5f6f8;
	border-top: 2rpx solid #f5f6f8;
	color: #a5a7b2;
}

.ucenter-line {
	width: 100%;
	height: 3px;
	background: #f4f5f6;
	margin: 16px 0;
}

.hello {
	display: flex;
	flex: 1;
	flex-direction: column;
}

.title {
	color: #8f8f94;
	margin-top: 50upx;
}

.ul {
	font-size: 30upx;
	color: #8f8f94;
	margin-top: 50upx;
}

.ul > view {
	line-height: 50upx;
}

.percenpading {
	padding-right: 100upx;
}
.grace-card-view{margin-top:30rpx; margin-bottom:8rpx; border-radius:10rpx; background-color:#FFFFFF; padding:10rpx 25rpx;}

.body{
	border-bottom: 0;
	padding-bottom: 5rpx;
}
</style>
